/* Cải thiện hiển thị hình ảnh */
.king-image-container {
  flex: 0 0 300px;
  text-align: center;
}

.king-portrait {
  max-width: 100%;
  max-height: 400px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  object-fit: contain;
}

/* Style cải tiến cho nội dung Wikipedia */
.wiki-content blockquote {
  margin: 16px 0;
  padding: 10px 16px;
  background-color: rgba(0, 0, 0, 0.03);
  border-left: 4px solid #1890ff;
  color: rgba(0, 0, 0, 0.85);
  font-style: italic;
}

.wiki-content .reference {
  font-size: 10px;
  vertical-align: super;
  color: #1890ff;
  margin: 0 2px;
}

.king-detail-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
