  border-top: 1px solid #eee;
}

.king-detail-source {
  font-size: 13px;
  color: #666;
}

.king-detail-source a {
  color: #1890ff;
  text-decoration: none;
}

.king-detail-source a:hover {
  text-decoration: underline;
} 